
/**
 * Breytir array me key objects yfir  2D array n key objects
 * 
 * @param {Array<{
 *  Flokkanmer: number,
 *  Undirflokkur: string,
 *  Erfileikastig: number,
 *  Gastig: number,
 *  Spurning: string,
 *  Svar: string }>} questions - Spurningarlisti me key objects  til a breyta  "hreinan" array fyrir spurningarleik
 * @returns {Array<Array<string|number>>} - 2D array me spurningarlistanum n key object
 */
export function convertObjecttoArray(questions) {
    const convertQ = questions.map(q => [
        q.Flokkanmer, 
        q.Undirflokkur,
        q.Erfileikastig,
        q.Gastig,
        q.Spurning,
        q.Svar
    ]);
    console.log(convertQ);
    return convertQ;
}