
// skilgreina path a spurninga database 
const flokkar = ["Flokkanmer", "Undirflokkur", "Erfileikastig", "Gastig", "Spurning", "Svar"];
/**
 * Fall sem tekur inn array rows og mappar t hvert stak sem 
 * object tilheyrandi kvenum flokki sem er skilgreint  "flokkar" listanum
 * 
 * @param {Array<string|number|undefined|null>} row - row r csv gagnagrunni sem varpast  key object
 * @returns {Object<string, string|number>} - key object array ar sem hver key er r "flokkar" og gildin eru r row
 */
export function mapRowToObject(row) {
    const object = {};
    flokkar.forEach((key, index) => {
        object[key] = row[index] ?? "";
    });
    return object;
}